.panel-title,.panel-title,.draggable{
    cursor: move;    
}
.panel{
    position: absolute;
    color: #333;
    width: 160px;
    margin: 20px 10px;
    transition: all 0.5s;
    
}

.panel .panel-body{
    min-height: 50px;
    outline: none;
}

.draggable {
    opacity: 0.8;
    transition: none;
}


